@import "../../../assets/scss/base/setting";
@import "../../../assets/scss/mixin/mixins";
@import "../../../assets/scss/mixin/basic";
@import "../../../assets/scss/mixin/border";

@include b(infinite-scroll) {
    @include e(loading) {
        position: relative;
        height: pxTorem(80);
        line-height: pxTorem(80);
        font-size: pxTorem(24);
        text-align: center;
        color: var(--daq-gray-4);

        .loading {
            position: relative;
            top: -2px;
            display: inline-block;
            margin-right: pxTorem(10);
            vertical-align: middle;
            width: pxTorem(28);
            height: pxTorem(28);
            border: 1px solid #999;
            border-right-color: transparent;
            box-sizing: border-box;
            border-radius: 50%;
            animation: rotate 1s infinite linear;
        }

        @keyframes rotate {
            0% {
                -webkit-transform: rotate(0);
                transform: rotate(0);
            }

            100% {
                -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
            }
        }
    }

    @include e(nodata) {
        text-align: center;
        font-size: pxTorem(24);
        line-height: pxTorem(80);
        color: var(--daq-gray-4);
    }
}